import React from "react";
import { Col, Container, Row, Footer, Fa } from "mdbreact";

class FooterPage extends React.Component {
  render() {
    return (
      <Footer color="mdb-color darken-3" className="font-small pt-4 mt-4">
        <Container fluid className="text-center text-md-left">
          <Row>
            <Col md="6" className="white-text">
              <h4 className="title" style={{ color: "#ffa000" }}>
                <strong>LODESTAR-SOFT</strong>
              </h4>
              <blockquote>OUR LIGHT WILL SHINE EVERYWHERE SOON</blockquote>
            </Col>
            <Col md="6">
              <ul>
                <li className="list-unstyled">
                  <a className="white-text" href="/about">
                    About
                  </a>
                </li>
                <li className="list-unstyled">
                  <a className="white-text" href="/contact">
                    Contact
                  </a>
                </li>
                <li className="list-unstyled">
                  <a className="white-text" href="/projects">
                    Projects
                  </a>
                </li>
              </ul>
            </Col>
          </Row>
        </Container>
        <div className="footer-copyright py-3 text-center">
          <Container className="white-text">
            &copy; {new Date().getFullYear()} Copyright:{" "}
            <a href="http://www.lodestar-soft.com"> lodestar-soft.com </a>
            <a href="https://www.facebook.com/lodestarForAll/">
              <Fa
                icon="facebook"
                className="mr-1"
                style={{ color: "#ffa000" }}
              />
            </a>
            <Fa
              icon="instagram"
              className="mr-1"
              style={{ color: "#ffa000" }}
            />
            <Fa icon="twitter" className="mr-1" style={{ color: "#ffa000" }} />
          </Container>
        </div>
      </Footer>
    );
  }
}

export default FooterPage;
