import React from "react";
import {
  Navbar,
  NavbarBrand,
  NavbarNav,
  NavItem,
  NavbarToggler,
  Collapse,
  Mask,
  Row,
  Col,
  View,
  Container
} from "mdbreact";
import Logo from "../assets/images/web_1_w.png";
import "../assets/scss/AppPage.scss";

class AppPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
      isWideEnough: false
    };
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.setState({
      collapse: !this.state.collapse
    });
  }

  handleTogglerClick = () => {
    this.setState({
      collapsed: !this.state.collapsed
    });
  };

  render() {
    const overlay = (
      <div
        id="sidenav-overlay"
        style={{ backgroundColor: "transparent" }}
        onClick={this.handleTogglerClick}
      />
    );
    return (
      <div id="apppage">
        <div>
          <Navbar
            color="mdb-color darken-3"
            transparent
            // style={{ backgroundColor: "#1d2951" }}
            dark
            expand="md"
            fixed="top"
            scrolling
          >
            <Container>
              <NavbarBrand href="/lodestar#">
                <img src={Logo} height="40" alt="" />
              </NavbarBrand>
              {!this.state.isWideEnough && (
                <NavbarToggler onClick={this.onClick} />
              )}
              <Collapse isOpen={this.state.collapsed} navbar>
                <NavbarNav right>
                  <NavItem active>
                    <a href="/lodestar/#services">
                      <strong>Services</strong>
                    </a>
                  </NavItem>
                  <NavItem>
                    <a href="/lodestar/#about">
                      <strong>About</strong>
                    </a>
                  </NavItem>

                  <NavItem>
                    <a href="/lodestar/#projects">
                      <strong>Projects</strong>
                    </a>
                  </NavItem>
                  <NavItem>
                    <a href="/lodestar/#contact">
                      <strong>Contact</strong>
                    </a>
                  </NavItem>
                </NavbarNav>
              </Collapse>
            </Container>
          </Navbar>
          {this.state.collapsed && overlay}
        </div>

        <View>
          <Mask className="d-flex justify-content-center align-items-center gradient">
            <Container>
              <Row>
                <div className="white-text text-center text-md-left col-md-6 mt-xl-5 mb-5">
                  <br />
                  <h1 className="h1-responsive font-weight-bold mt-sm-5">
                    LODESTAR - SOFT
                  </h1>
                  <h2 className="h2-responsive font-weight-bold mt-sm-5">
                    Our Light Will Shine Everywhere
                  </h2>
                  <hr className="hr-light" />
                  <h5 className="mb-4 orange-text">
                    We believe in the importance of human in society and from
                    here we work to light your way and employ modern technology
                    to suit your professional aspirations and goals of your
                    organization. Lodestar believes in her ability to innovate
                    and convey your ideas to a reality that serves the community
                    and the human being.
                  </h5>
                </div>
                <Col md="6" xl="5" className="mt-xl-5">
                  <img
                    src="https://mdbootstrap.com/img/Mockups/Transparent/Small/admin-new.png"
                    alt=""
                    className="img-fluid"
                  />
                </Col>
              </Row>
            </Container>
          </Mask>
        </View>
      </div>
    );
  }
}

export default AppPage;
